.wrapper {
  width: 100vw;
  height: 100vh;
  padding: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  background: #527593;
  background: -webkit-linear-gradient(left, #527593 0%, #5e3986 100%);
  background: -webkit-gradient(linear, left top, right top, from(#527593), to(#5e3986));
  background: -o-linear-gradient(left, #527593 0%, #5e3986 100%);
  background: linear-gradient(to right, #527593 0%, #5e3986 100%);
}

* {
  margin: 0px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  list-style-type: none;
}

.title {
  font-size: 250%;
  text-align: center;
  margin-bottom: 50px;
  color: white;
  text-shadow: 3px 3px 2px rgba(150, 150, 150, 0.5);
  -ms-flex: 0 1 auto;
      flex: 0 1 auto;
}

.container {
  width: 800px;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

input {
  padding: 10px;
}

.header {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  margin-bottom: 40px;
}

.header input,
.header > .option > * {
  border-radius: 3px;
}

.option {
  display: -ms-flexbox;
  display: flex;
}

.option > * {
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-preferred-size: 0;
      flex-basis: 0;
}

.option > select {
  margin-right: 10px;
}

.main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  border-radius: 3px;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
  border: 1px solid #ededed;
  color: white;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
}

.main-grid {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  min-height: 0;
}

.income-type {
  text-align: center;
  padding: 5px;
  font-family: sans-serif;
  font-weight: 600;
  background: #55608e;
  border: 1px solid #ededed;
}

.info-box {
  width: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.item-list {
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  border: 1px solid #ededed;
  overflow-y: auto;
}

.item {
  display: grid;
  grid-template-columns: 2fr 1fr 3fr;
}

.item > * {
  padding: 8px;
}

.total-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.total-grid > * {
  border: 1px solid #ededed;
}

.final {
  grid-column: 1/-1;
  padding: 10px;
  text-align: center;
}



